.card {
  border-radius: 2px;
  margin: 1rem;
  position: relative;
  text-align: left;
}

.card:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}

.card > .img-container {
  overflow: hidden;
  text-align: center;
  
}
.card > .img-container > img {
  width: 100%;
  height: 100%;
}









